.success{
    color: green;
    background-color: lightyellow;
    font-size: 28px;
    border-style: solid;
    border-radius: 8px;
    padding: 20px;
    margin-top: 10px;
    margin-bottom: 10px;   
}

.error{
    color: red;
    background-color: lightgray;
    font-size: 28px;
    border-style: solid;
    border-radius: 8px;
    padding: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}